.Theme{
  position: relative;
}
.Theme__text{
  padding: 0 1rem;
  margin-bottom: 0.5rem;
}
.Theme__text span:nth-child(2){
  margin: 0 2rem;
}
.Theme__option{
  background-color: hsl(223, 31%, 20%);
  position: relative;

  height: 2.5rem;

  padding: 0.5rem;

  display: flex;

  border-radius: 2.5rem;
}
.Theme__option button{
  background-color: transparent;

  width: 100%;

  z-index: 100;

  border: none;
  outline: none;
}

.circle{
  background-color: hsl(6, 63%, 50%);
  position: absolute;

  width: calc(2.5rem - (0.5rem * 2));
  height: calc(2.5rem - (0.5rem * 2));

  z-index: 50;

  border-radius:100%;

  left: 0.5rem;
  
  transition-duration: 0.5s;
}