.Keyboard{
  background-color: hsl(223, 31%, 20%);

  width: 100%;

  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: repeat(4, 1fr);
  grid-gap: 2rem;

  border-radius: 1rem;
  padding: 2rem;
}

.Key{
  background-color:hsl(30, 25%, 89%);

  text-align: center;
  font-size: 3.2rem;
  font-weight: 700;
  color: hsl(224, 28%, 35%);

  padding: 1rem;

  border-radius: 1rem;

  box-shadow: 0px 5px 0px 0px hsl(28, 16%, 65%);

  transition-duration: 0.5s;
}

.Key:nth-last-child(1){
  grid-row: 5 / 6;
  grid-column: 3 / 5;
}
.Key:nth-last-child(2){
  grid-row: 5 / 6;
  grid-column: 1 / 3;
}
.Key--blue{
  background-color: hsl(225, 21%, 49%);
  color: hsl(0, 0%, 93%);

  box-shadow: 0px 5px 0px 0px hsl(224, 28%, 35%);
}
.Key--red{
  background-color: hsl(6, 63%, 50%);
  color: hsl(0, 0%, 93%);

  box-shadow: 0px 5px 0px 0px hsl(6, 70%, 34%);
}

.Key:hover{
  background-color:hsl(0, 0%, 100%);
  cursor: pointer;
}
.Key--blue:hover {
  background-color:#a2b3e1;
  cursor: pointer;
}
.Key--red:hover {
  background-color:#f96c5b;
  cursor: pointer;
}