.Display{
  position: relative;
  background-color:  hsl(224, 36%, 15%);

  width: 100%;

  margin: 2rem 0;
  padding: 2rem;

  text-align: right;

  border-radius: 1rem;
}
.Display input{
  background-color: transparent;
  width: 100%;

  color: hsl(0, 0%, 100%);
  font-size: 4rem;
  font-weight: 700;
  text-align: right;

  border: none;
  outline: none;
}
